@import '../../variables.module.scss';

.root {
    padding: 100px 0 25px;
    display: flex;
    justify-content: center;
}

.container {
    padding: 0 30px;
    width: $container-width;
    box-sizing: border-box;
}

.title {
    font-weight: normal;
    font-size: 3rem;
    letter-spacing: .2rem;
    text-transform: uppercase;
    text-align: center;
    color: var(--theme-font);
    margin-bottom: 30px;
}

.title > span {
    color: $theme-color;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 30px;
    padding: 40px 0;
}

.content > p {
    text-align: center;
    font-size: 1.25rem;
    color: var(--theme-font);
}

.resume {
    display: block;
    padding: 10px 60px;
    color: var(--theme-font);
    // color: #fff;
    font-size: 1.25rem;
    border: 2px solid $theme-color;
    text-transform: uppercase;
    transition: background-color .3s, color .3s;
    margin-top: 30px;

    &:hover {
        background-color: $theme-color;
        color: #fff;
    }
}

