@import '../../variables.module.scss';

.root {
    padding: 100px 0 25px;
    display: flex;
    justify-content: center;
}

.container {
    padding: 0 30px;
    width: $container-width;
    box-sizing: border-box;
}

.title {
    font-weight: normal;
    font-size: 3rem;
    letter-spacing: .2rem;
    text-transform: uppercase;
    text-align: center;
    color: var(--theme-font);
    margin-bottom: 30px;
}

.title > span {
    color: $theme-color;
}

.content {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    row-gap: 30px;
    padding: 40px 0;
}

.block {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 10px;
    padding: 30px;
    min-width: 200px;
}

.block > h3 {
    color: var(--theme-font);
    font-size: 1.5rem;
    font-weight: normal;
}

.block p {
    color: var(--theme-font);
    font-size: 1rem;
    text-align: center;
}
