@import '../../variables.module.scss';

.root {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    display: flex;
    justify-content: center;
    background-color: transparent;
    transition: background-color .3s ease;
    z-index: 999;

    &.active {
        background-color: $header-color;

        .navigation > ul > li > a {
            transition: color .3s;
            color: #fff;

            &:hover {
                color: $theme-color;
            }
        }

        .brand {
            transition: color .3s;
            color: #fff;
        }

        .button {
            &::after,
            &::before {
                background-color: #fff;
            }
        }
    }
}

.navigation {
    padding: 0 30px;
    width: $container-width;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 30px;
}

.navigation > ul {
    display: flex;

    @media screen and (max-width: $medium) {
        position: fixed;
        top: 0;
        left: 100%;
        width: 100%;
        bottom: 0;
        background-color: $mobile-header-color;
        transition: left .3s;

        overflow: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &.active {
            left: 0;

            li > a {
                color: #fff;
            }
        }
    }
}

.navigation > ul > li > a {
    display: block;
    padding: 25px;
    // color: #fff;
    color: var(--theme-font);
    font-size: 1.25rem;
    letter-spacing: .2rem;
    text-transform: uppercase;

    &:hover {
        color: $theme-color;
    }
}

.brand {
    font-weight: normal;
    font-size: 1.75rem;
    // color: #fff;
    color: var(--theme-font);
    letter-spacing: .15rem;
    word-spacing: .5rem;
    text-transform: uppercase;
}

.brand > span {
    color: $theme-color;
}

.button {
    appearance: none;
    display: none;
    padding: 27px;
    margin: 7.5px;
    z-index: 1;
    border: 2px solid $theme-color;
    border-radius: 50%;
    cursor: pointer;
    background-color: transparent;
    position: relative;

    &::before,
    &::after {
        content: '';
        width: 25px;
        height: 2px;
        background-color: var(--theme-font);
        display: block;
        position: absolute;
        transform: translate(-50%, -50%);
        transition: margin, .3s, transform .3s;
    }

    &::before {
        margin-top: -4px;
    }

    &::after {
        margin-top: 4px;
    }

    &.active {        
        &::before,
        &::after {
            margin: 0;
            background-color: #fff;
        }
    }

    @media screen and (max-width: $medium) {
        display: block;
    }
}

.moon,
.sun {
    appearance: none;
    height: 60px;
    width: 60px;
    cursor: pointer;
    border: none;
    background-color: transparent;
    position: fixed;
    left: 10px;
    bottom: 10px;
    border-radius: 50%;
    z-index: -1;
}

.moon {
    background-image: url('moon.svg');
}

.sun {
    background-image: url('sun.svg');
}
