@import '../../variables.module.scss';

.root {
    padding: 100px 0 25px;
    display: flex;
    justify-content: center;
}

.container {
    padding: 0 30px;
    width: $container-width;
    box-sizing: border-box;
}

.title {
    font-weight: normal;
    font-size: 3rem;
    letter-spacing: .2rem;
    text-transform: uppercase;
    text-align: center;
    color: var(--theme-font);
}

.title > span {
    color: $theme-color;
}

.projects {
    margin: 30px 0;
    display: flex;
    gap: 30px 30px;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.project {
    background: linear-gradient(to right, $header-color, $skills-block-color);
    border-radius: 10px;
    padding: 30px;

}

.project > h3 {
    font-size: 2.75rem;
    text-align: center;
    color: #fff;
}

.project > p {
    text-align: center;
    padding: 15px 0;
    color: #fff;
}

.applications {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    gap: 30px 30px;
    justify-content: center;
}

.application {
    min-width: 200px;
    padding: 15px;

    @media all and (max-width: $medium) {
        flex: 1 1;
    }
}

.application > a {
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: bold;
    display: inline-flex;
    align-items: center;
    column-gap: 10px;
    cursor: pointer;
    color: #fff;
    justify-items: flex-start;

    text-decoration: underline;

    > svg {
        fill: $theme-color;
    }

    &:hover {
        color: $theme-color;
    }

    &:active {
        color: $theme-active-color;

        > svg {
            fill: $theme-active-color;
        }
    }
}

.application > ul {
    margin-top: 30px;
}

.application > ul > li {
    list-style: disc;
    font-size: 1.25rem;
    margin-left: 20px;
    color: #fff;
    box-sizing: border-box;
}
