@import '../../variables.module.scss';

.root {
    height: 100vh;
    // background-image: url('back.jpg');
    // background-repeat: no-repeat;
    // background-size: cover;
    background-position-y: 60%;
    display: flex;
    justify-content: center;
    overflow: hidden;
    position: relative;
    z-index: 0;

    background-color: var(--theme-background);

    // &::after {
    //     content: '';
    //     position: absolute;
    //     top: 0;
    //     left: 0;
    //     right: 0;
    //     bottom: 0;
    //     // backdrop-filter: blur(6px);
    //     // -webkit-backdrop-filter: blur(6px);
    //     z-index: -1;
    // }
}

.container {
    padding: 0 30px;
    width: $container-width;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container > div {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@keyframes welcome_anim {
    to {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes introduction_anim {
    to {
        opacity: 1;
    }
}

.welcome,
.introduction {
    color: var(--theme-font);
    // color: #fff;
    font-weight: normal;
    letter-spacing: 0.25rem;
    font-size: 4rem;
    opacity: 0;
    text-align: center;
}

.welcome {
    transform: scale(3);
    animation: welcome_anim 1s ease-in-out forwards;
}

.introduction {
    animation: introduction_anim 1s ease-in-out 1s forwards;
}

.introduction > span {
    color: $theme-color;
}

.portfolio {
    display: block;
    padding: 10px 60px;
    color: var(--theme-font);
    // color: #fff;
    font-size: 1.75rem;
    border: 2px solid $theme-color;
    text-transform: uppercase;
    transition: background-color .3s, color .3s;
    margin-top: 30px;

    &:hover {
        background-color: $theme-color;
        color: #fff;
    }
}
