@import '../../variables.module.scss';

.root {
    display: flex;
    justify-content: center;
    background-color: $skills-block-color;
}

.container {
    padding: 30px;
    width: $container-width;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    row-gap: 30px;
}

.brand {
    text-align: center;
    font-weight: normal;
    font-size: 1.75rem;
    color: #fff;
    letter-spacing: .15rem;
    word-spacing: .5rem;
    text-transform: uppercase;
}

.brand > span {
    color: $theme-color;
}

.container > p {
    text-align: center;
    font-size: 1rem;
    color: #fff;
}
