@import '../../variables.module.scss';

.root {
    padding: 100px 0 25px;
    display: flex;
    justify-content: center;
}

.container {
    padding: 0 30px;
    width: $container-width;
    box-sizing: border-box;
}

.container > p {
    text-align: center;
    padding: 15px 0;
    color: var(--theme-font);
}

.title {
    font-weight: normal;
    font-size: 3rem;
    letter-spacing: .2rem;
    text-transform: uppercase;
    text-align: center;
    color: var(--theme-font);
}

.title > span {
    color: $theme-color;
}

.skills {
    margin: 30px 0;
    display: flex;
    row-gap: 30px;
    column-gap: 30px;
    flex-wrap: wrap;
    align-items: flex-start;
}

.block {
    flex: 1 1;
    width: 50%;
    min-width: 200px;
    padding: 30px;
    border-radius: 10px;
    position: relative;
    background-image: url('../home/back.jpg');
    background-size: cover;
    overflow: hidden;
    z-index: 0;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: $skills-block-color;
        opacity: 0.35;
        z-index: -1;
    }
}

.block > h3 {
    font-size: 1.5rem;
    text-transform: uppercase;
    color: $theme-color;
}

.block > ul {
    margin-top: 10px;
}

.block > ul > li {
    color: #fff;
    font-size: 1.25rem;
    list-style: disc;
    margin-left: 20px;
}
